:root,
[data-theme=light] {
  --primary: #0e0d0d;
  --secondary: #8b0803;
  --secondary-color: var(--secondary);
  --primary-color: var(--primary);
  --btn-height: 28px;
  /* // Checkbox */
  --checkbox-right-margin: var(--margin-xs);
  --checkbox-size: 14px;
  --checkbox-focus-shadow: 0 0 0 2px var(--gray-300);
  --checkbox-gradient: linear-gradient(
    180deg,
    --secondary-color,
    -124.51%,
    var(--primary) 100%
  );
}

.for-forgot,
.for-login {
  display: flex;
  justify-content: space-between;
  padding: 0;
}
.for-forgot .auth-form,
.for-login .auth-form {
  width: 100%;
  max-width: 375px;
  margin: auto 40px;
}
.for-forgot .auth-form .page-card,
.for-login .auth-form .page-card {
  padding: 45px;
}
.for-forgot .auth-form .page-card .page-card-head,
.for-login .auth-form .page-card .page-card-head {
  margin-bottom: 1.5rem;
}
.for-forgot .auth-form .page-card .page-card-head img,
.for-login .auth-form .page-card .page-card-head img {
  max-height: 150px;
}
.for-forgot .auth-bg,
.for-login .auth-bg {
  flex: 1;
  height: 100vh;
  background-image: url("/assets/nextash/images/sideimage.jpg");
  background-position: center center;
  background-size: cover;
}